.star {
    margin: auto;
    position: relative;
    width: 100%;
    height: 16em;
    bottom: -100px;
  }
  .star__stroke {
    --dur: 2s;
    animation: stroke1 var(--dur) linear infinite;
  }
  .star__stroke--2 {
    animation-name: stroke2;
    animation-delay: calc(var(--dur) / -2);
  }
  .star__stroke--3 {
    animation-name: stroke3;
    animation-direction: reverse;
  }
  .star__stroke--4 {
    animation-name: stroke4;
    animation-delay: calc(var(--dur) / -2);
    animation-direction: reverse;
  }
  
  @keyframes stroke1 {
    from {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: 124;
    }
  }
  @keyframes stroke2 {
    from {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: 248;
    }
  }
  @keyframes stroke3 {
    from {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: 372;
    }
  }
  @keyframes stroke4 {
    from {
      stroke-dashoffset: 0;
    }
    to {
      stroke-dashoffset: 496;
    }
  }
  
